body{overflow-x:hidden;font-family:"Inconsolata";background-color:#f5feff}.about-me-container{display:-webkit-flex;display:flex}.about-me-container .whoami-pic{width:15rem;padding-top:.5rem;padding-bottom:.5rem}.about-me-container .whoami-skills{display:-webkit-flex;display:flex}.about-me-container .section{padding-right:0rem}.nav-container{display:-webkit-flex;display:flex;padding-top:2.5rem;padding-left:2.5rem;width:100%}.nav-container .nav-item{display:-webkit-flex;display:flex;padding-right:2rem}.nav-container .nav-item .nav-dot{padding-right:.5rem}.nav-container .nav-item .nav-title:hover{color:gray;cursor:pointer}.nav-container .nav-item .nav-title{padding-top:.05rem;text-decoration:none;color:#000}.whoami-container{padding-left:2.5rem;padding-top:2.5rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%}.whoami-container .whoami-title{font-family:"Farro";font-size:1.5rem}.whoami-container .icon-container{display:-webkit-flex;display:flex}.whoami-container .icon-container .emoji{margin-bottom:.25rem}.whoami-container .icon-container .emoji-title{margin-top:.25rem}.whoami-container .icon-container #soft-dev{padding-left:.15rem}.whoami-container .icon-container #san-diego{padding-left:.25rem}.whoami-container .icon-container .icon{padding-right:1rem;padding-top:1rem;width:2rem}.skills-title{font-size:1.25rem;font-family:"Farro";padding-top:2.5rem;padding-left:2.5rem}.all-skills-container{padding-left:6rem}.all-skills-container .skills-container{padding-left:2.5rem;width:100%;display:grid}.all-skills-container .skills-container .skill{padding-right:8rem;padding-bottom:1rem}.all-skills-container .skills-container .skill .sub-title{padding-top:.5rem;padding-bottom:.5rem}.skill-icon-container{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;padding-bottom:1rem}.skill-icon-container .skill-icon{width:1rem;padding-right:.25rem}.experience-container{width:100%;padding-top:2.5rem}.experience-container .experience-title{font-family:"Farro";font-size:1.25rem;padding-bottom:.5rem}.experience-container .job-title{font-size:1.25rem}.experience-container .job-title a{text-decoration:none;color:blue}.experience-container .job-title a:hover{color:#d3d3d3}.experience-container ul{padding-left:1.15rem;margin-top:.25rem}.projects-container{padding-left:2.5rem;padding-top:2.5rem}.projects-container .projects{padding-bottom:4rem}.projects-container .projects-title{font-family:"Farro";font-size:1.25rem;padding-bottom:.5rem}.project-container{display:grid;grid-template-columns:18rem 35rem 10rem auto}.project-container .project-img-bg{background-color:#d3d3d3;border:1px solid #000;height:12rem;width:12rem}.project-container .project-img-bg .project-img{width:12rem;height:12rem;padding:0;margin:0}.project-container .project-section .project-description{padding-top:1rem;margin-right:4rem;font-size:.9rem;text-indent:1.5rem}.project-container .project-section .contributors{padding-top:1rem}.project-container .project-section .contributors ul{margin-top:0;padding:0}.project-container .project-section .contributors li{padding-bottom:1rem}@media only screen and (max-width: 1426px){.about-me-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.experience-container{width:100%;padding-left:2.5rem}.skills-container{grid-template-columns:auto auto auto}}@media only screen and (max-width: 1024px){.project-container{grid-template-columns:auto auto}.project-container .project-section{padding-bottom:2rem;padding-right:2rem}.project-container .project-section .project-description{margin:0;padding-top:1rem}.skills-container{grid-template-columns:auto}}@media only screen and (max-width: 425px){.experience-container{width:100%;padding:0;margin:0}}@media only screen and (max-width: 768px){.whoami-skills{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.experience-container{width:100%;padding-left:2.5rem;margin:0}.project-container{grid-template-columns:auto auto}.projects-container{padding-left:1rem;padding-top:1rem}.nav-container{padding-left:1rem;padding-top:1rem}.section-container{padding-right:0rem}.whoami-container{padding-top:1rem}.skills-title{padding-top:1rem}.skills-container{padding-left:1rem}.all-skills-container{padding-left:0}}@media only screen and (max-width: 680px){.project-container{grid-template-columns:auto}}
